.footer__container {
    z-index: 10;
    background-color: #7510f7;
    height: 80vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.img__brand {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
}

.living {
    color: #fff;
    font-family: 'Quicksand', sans-serif;
    font-weight: 500;
    text-align: center;
    width: 55%;
    font-size: 20px;
}

.social-media {
    display: flex;
    gap: 25px;
}

.items {
    color: #fff;
    margin-top: 50px;
    font-size: 25px;
    width: 50px;
    height: 50px;
    border: 2px solid #9f58fa;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.items:hover {
    cursor: pointer;
    background-color: #fff;
    color: #7510f7;
    transition: all .2s ease-in-out;

}

.end-footer>p {
    font-family: 'Quicksand', sans-serif;
    color: #fff;
    margin-top: 4em;
}


@media only screen and (max-width: 770px) {
    .start {
        display: none;
    }

    
}